import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { CONFIG } from 'src/app/config';
import { API } from 'src/app/enums/api.enum';
import { Observable } from 'rxjs';
import { of } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class WizardService {
  config: typeof CONFIG = CONFIG;
  _profileUrl = this.config.getBaseURL(API.RESOURCES) + "/profile/";
  _saveUrl = this.config.getBaseURL(API.RESOURCES) + "/profile/write";
  _viewUrl = this.config.getBaseURL(API.RESOURCES) + "/profile/view/1/";
  _previewUrl = this.config.getBaseURL(API.RESOURCES) + "/profile/preview/1/";
  _saveFileUrl = this.config.getBaseURL(API.RESOURCES) + "/profile/file/add";
  _deleteFileUrl = this.config.getBaseURL(API.RESOURCES) + "/profile/file/delete";
  _googleFontsUrl = "https://www.googleapis.com/webfonts/v1/webfonts?sort=popularity";
  _userSearchUrl = this.config.getBaseURL(API.RESOURCES) + "/user/search/";
  modifyIndex = -1;

  profile:any;
  constructor(
    public http: HttpClient
  ) {

  }

  setProfileData(profile:any) {
    this.profile = profile;
  }

  getProfileData() {
    return this.profile;
  }

  save(signupData: any) {
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "POST");
    return this.http.post(this._saveUrl, signupData, { headers: headers });
  }

  getViewData(id: any, isAuth:any = false) {
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');

    if (isAuth) {
      headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    }
    headers = headers.append("X-HTTP-Method", "GET");
    const _url = isAuth ? this._previewUrl: this._viewUrl;
    return this.http.get(_url+id, { headers: headers });
  }

  getGoogleFonts() {
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append("X-HTTP-Method", "GET");
    return this.http.get(this._googleFontsUrl, { headers: headers });
  }

  get(extendedUrl: any){
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
        ibssApp = JSON.parse(ibssApp);
    }

    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "GET");        
    return this.http.get(this._profileUrl + extendedUrl, { headers: headers });
  }

  searchUser(extendedUrl: any){
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
        ibssApp = JSON.parse(ibssApp);
    }

    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "GET");        
    return this.http.get(this._userSearchUrl + extendedUrl, { headers: headers });
  }

  assignUser(params: any){
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "PUT");
    return this.http.put(this._profileUrl+"assign/", params, { headers: headers });
  }

  saveFile(payload:any) {
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "POST");
    return this.http.post(this._saveFileUrl, payload, { headers: headers });
  } 

  deleteFile(payload:any): Observable<any> {
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }

    const currAssetsPath = this.config.assetsPath+this.profile.type+"/"+this.profile.title+"/";
    if (payload.filePath.toLowerCase().indexOf(currAssetsPath.toLowerCase())===-1) {
      const inputParams = {
        "serviceData": {
          "imageData": "",
          "parentProfileName": "",
          "profileCategory": "1",
          "profileName": this.profile.title,
          "profileType": this.profile.type,
          "rootProfileName": "",
          "sectionName" : payload.sectionName,
          "fileNames" : payload.fileNames,
        }
      }
      let headers: HttpHeaders = new HttpHeaders();
      headers = headers.append('Accept', 'application/json');
      headers = headers.append('Content-Type', 'application/json');
      headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
      headers = headers.append("X-HTTP-Method", "DELETE");
      // return this.http.post(this._deleteFileUrl, inputParams, { headers: headers });
    }

    return of({});
  }
}
